import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Table, Button, Checkbox } from 'semantic-ui-react';
import { DateInput } from '../../DateInput';
import { ModalExample } from './ModalUpdate';
import { Header, Modal, Icon } from 'semantic-ui-react'

export class RowTable extends Component {
    state = {
        modalOpen: false
    }

    changeTaskProp(propName, value) {
        this.props.updateTask(this.props.task.id, {
            [propName]: value
        });
    }

    drawRow = () => {
        const { task, removeTask } = this.props;
        return (
            <Table.Row onDoubleClick={this.handleOpen}>
                <Table.Cell>
                    <input type='checkbox'
                        checked={task.checked}
                        onChange={(ev) =>
                            this.changeTaskProp('checked', ev.target.checked)} />
                </Table.Cell>
                <Table.Cell>{task.title || '--title--'}</Table.Cell>
                <Table.Cell>{task.priority || '--priority--'}</Table.Cell>
                <Table.Cell><DateInput date={task.date} /></Table.Cell>
                <Table.Cell>
                    <Button onClick={() => removeTask(task.id)}>X
        </Button>
                </Table.Cell>
            </Table.Row>
        )
    }

    handleOpen = () => this.setState({ modalOpen: true })
    handleClose = () => this.setState({ modalOpen: false })

    render() {

        return (
            <Modal trigger={this.drawRow()}
                open={this.state.modalOpen}
                onClose={() => this.handleClose}>
                <Modal.Header>Modal</Modal.Header>
                <Modal.Content >
                    <Modal.Description>
                        <Header>{this.props.task.title}</Header>
                        <span>{this.props.task.description}</span>
                    </Modal.Description>
                </Modal.Content>
                <Modal.Actions>
                    <Button color='green' onClick={this.handleClose} inverted>
                        <Icon name='checkmark' />  
                     </Button>
                    <Button color='red' onClick={this.handleClose} inverted>
                        <Icon name='remove' />   
                    </Button>
                </Modal.Actions>
            </Modal>)
    }


}

RowTable.propTypes = {
    task: PropTypes.object,
    removeTask: PropTypes.func,
    updateTask: PropTypes.func
};



